
























































import { Component, Vue, Ref } from "vue-property-decorator";
import CallToAction from "../CallToAction.vue";
import FixedHeader from "../../components/header/FixedHeader.vue";
import Testimonials from "../Testimonials.vue";
import Api from "../../Api";

@Component({
  components: {
    CallToAction,
    FixedHeader,
    Testimonials,
  },
})
export default class OurPurpose extends Vue {
  purpose = {
    Activities: "",
    Contribution_Image: "",
    Contributions: "",
    Mission: "",
    Mission_Image: "",
    Video: "",
    Vision: "",
    id: 0,
  };

  async created() {
    await this.fetchPurpose();
  }

  async fetchPurpose() {
    await fetch(Api.OurPurpose.GetAll)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.purpose = data.data;
        this.purpose.Video = Api.LocalhostImages + this.purpose.Video;
        this.purpose.Mission_Image =
          Api.LocalhostImages + this.purpose.Mission_Image;
        this.purpose.Contribution_Image =
          Api.LocalhostImages + this.purpose.Contribution_Image;
      })
      .catch((error) => {
        console.log(error);
      });
  }
}
